import city from "../../util/city.js"
export default {
	data() {
		return {
			searchLetter: [],
			showLetter: "",
			windowHeight: 0,
			arr: [],
			cityList: [],
			isShowLetter: false,
			scrollIntoId: '', //跳转id
			city: "",
			allArr: [],
			hotcityList: [],
			current: this.value

		}
	},
	onLoad: function(e) {
		var that = this
		var hotcityList = ["广州", "珠海", "北京", "上海"]
		var searchLetter = city.searchLetter;
		var cityList = city.cityList();
		var cityA = []
		var cityB = []
		var cityName = []
		for (var item of cityList) {
			cityA.push(item)
		}
		for (var i = 0; i < cityA.length; i++) {
			cityB.push(cityA[i].cityInfo)
		}

		var allArr = []
		for (var i = 0; i < cityB.length; i++) {
			var citys = {}
			var arr = [];
			for (var a = 0; a < cityB[i].length; a++) {
				cityName.push(cityB[i][a].city)
				arr.push(cityB[i][a].city)
			}
			citys.lists = arr
			citys.letter = searchLetter[i]
			allArr.push(citys)
		}
		this.cityName = cityName;
		this.allArr = allArr;
		this.hotcityList = hotcityList
	},

	mounted() {
		this.getSystemInfo()
	},
	// 搜索
	methods: {
		// 计算高
		getSystemInfo() {
			uni.getSystemInfo().then(res => {
				let [error, data] = res
				this.windowHeight = `${data.windowHeight}px`
			})
		},
		// 跳转搜索
		jumpSearch(e){
			uni.navigateTo({
				url: '../search/search'
			});
		},
		// 搜索城市
		searchList(e) {
			console.log(111)
			var cityName = this.cityName
			var value = e.target.value
			if (value.length != 0) {
				var arr = [];
				for (var i in cityName) {
					if (cityName[i].match(value) != null) {
						arr.push(cityName[i]);
					}
				}
				this.arr = arr
				if (arr.length != 0) {
					this.input = true
				}
			} else {
				this.arr = []
				this.input = false
			}
		},
		// 移动
		scrollTo(letter) {
			this.scrollIntoId = letter === '热门' ? 'current' : letter
			uni.showToast({
				icon: "none",
				title: letter,
				duration: 2000
			});
		},
		//选择城市
		bindCity(e) {
			this.city = e
			console.log("选择的城市是:" + e)
			getApp().globalData.city = e
			uni.navigateBack({
				delta: 1,
				animationType: 'pop-out',
				animationDuration: 200
			})
		},
	}
}
